.popup {
  display: flex;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.3s linear;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  z-index: 11;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba( 0, 0, 0, 0.5);
}

